<script>
import { login, getInfo, logout } from '@/api/login'

export default {
  data() {
    return {
      id: 0,
      managerId:0,
      entId:0,
      loginBtn: false,
      // login type: 0 email, 1 username, 2 telephone
      loginType: 0,
      isLoginError: false,
      form: this.$form.createForm(this),
      state: {
        time: 60,
        loginBtn: false,
        // login type: 0 email, 1 username, 2 telephone
        loginType: 0,
        smsSendBtn: false
      },
        showInfo:0,
        info:"初始密码为123456",
      img:require('../../assets/dddb-webEnt/A-worker-cardbg.png'),
      imgbg:require('../../assets/dddb-webEnt/A-homepage-bg.png'),

    };
  },
  created() {
    this.$parent.showSiderMenu = false;
    this.$parent.showMenu = false;
  },
  computed: {
    // cooimgsrc(){
    //   let list = [
    //     {
    //       imgSource: "../../assets/dddb-webEnt/company-01.jpg"
    //     },
    //     {
    //       imgSource: "../../assets/dddb-webEnt/company-01.jpg"
    //     }
    //   ];
    //   return list;
    // },
  },
  watch: {
    $route: function(to, from) {
      this.selectedKeys = [to.path];
      console.log();
    }
  },
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: 'normal_login' });
  },
  methods: {
    handleUsernameOrEmail (rule, value, callback) {
      const { state } = this
      const regex = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/
      if (regex.test(value)) {
        state.loginType = 0
      } else {
        state.loginType = 1
      }
      callback()
    },
    handleSubmit (e) {
      e.preventDefault()
      // const validateFieldsKey = ['username', 'password', 'user_type']
      const { form: { validateFields } } = this
      validateFields((err, values) => {
        if (!err) {
          console.log('login form', values)
          const loginParams = { ...values }
          const that = this
          login(loginParams)
          .then(function (response) {
            if (response.data.result === 25) {
              console.log(response.data)
              localStorage.removeItem('userId')
              localStorage.removeItem('entId')
              localStorage.removeItem('mangerId')
              localStorage.setItem('userId', response.data.user.id)
              that.id = Number(localStorage.getItem('userId'))
              // 管理员跳转界面
              if (response.data.user_type === 'mag') {
                console.log(response.data)
                localStorage.setItem('mangerId', response.data.user.id)
                that.$router.push({ path: '/entlist' })
                } else if (response.data.user_type === 'ent') {
                  // 登录成功后跳转到企业端
                  localStorage.setItem('entId', response.data.user.id)
                  that.entId = Number(localStorage.getItem('entId'))
                  that.$router.push({ path: '/index' })
                }
              console.log(localStorage.getItem('userId'))
            }
            else if (response.data.result === 40) {
                alert(response.data.msg)
              }
          })
          .catch(err => {
            console.log(err)
          })
          .finally(()=>{
            console.log('!!!!')
          })

        } else {
          setTimeout(() => {
            state.loginBtn = false
          }, 600)
        }
      })
    },
    loginSuccess (res) {
      console.log(res.data)
      console.log(JSON.parse(res.body))
      console.log('xxxxxx')

      this.$router.push({ path: '/Home' })
      window.location.href = '/Home'
      // 延迟 1 秒显示欢迎信息
      setTimeout(() => {
        this.$notification.success({
          message: '欢迎',
          description: `${timeFix()}，欢迎回来`
        })
      }, 1000)
      this.isLoginError = false
    },
    requestFailed (err) {
      console.log('111111')
      this.isLoginError = true
      this.$notification['error']({
        message: '错误',
        description: ((err.response || {}).data || {}).message || '请求出现错误，请稍后再试',
        duration: 4
      })
    }
  },
};
</script>

